import { Component, OnInit } from '@angular/core';
import { PopoverController, ActionSheetController, ModalController, NavParams } from '@ionic/angular';
import { UsersModalPage } from 'src/app/users/teams/users-modal/users-modal.page';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Router } from '@angular/router';
import { Utils } from 'src/commons/Utils';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Team } from 'src/models/Team';
import { User } from 'src/models/User';
import { ToastText, MenuText, ErrorMessage } from 'src/commons/Constants';

@Component({
  selector: 'app-new',
  templateUrl: './new.page.html',
  styleUrls: ['./new.page.scss'],
})
export class NewPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "NewPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public modoEdicao: boolean = false;
  private team: Team;
  private users: Array<User> = new Array<User>();
  public userList: Array<User> = new Array<User>();
  private searchTerm: string;
  public teamLeader: User;
  public name: string;
  public admin: boolean;
  public imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    public actionSheetController: ActionSheetController,
    public modalController: ModalController,
    private pageDataService: PageDataService,
    private navParams: NavParams) {
    let team = this.navParams.get('team') as Team;
    let admin = this.navParams.get('admin') as Boolean;
    this.admin = admin == true;
    this.modoEdicao = !Utils.isVarInvalid(team);
    if (this.modoEdicao) {
      this.api.getTeamById(team.getId()).then((newTeam: Team) => {
        this.team = newTeam;
        this.name = this.team.getNome();
        this.users = this.team.getMembros();
        this.userList = this.users;
        this.api.getUserById(team.getLiderId()).then((user: User) => {
          this.teamLeader = user;
        }).catch((error) => {
          this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
        });
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
      });
    }
    else {
      this.team = new Team('mock', [], undefined, undefined)
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async addNewLider() {
    const modal = await this.modalController.create({
      component: UsersModalPage,
      backdropDismiss: false,
      componentProps: {
        team: this.team,
        leader: true
      }
    });
    modal.present()
    modal.onDidDismiss().then(() => {
      this.imageConcatString = Utils.generateRandomString();
      let data = this.pageDataService.getData('selectedUsers');
      this.pageDataService.removeData('selectedUsers');
      if (!Utils.isArrayInvalid(data)) {
        this.teamLeader = data[0];
      }
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async addNewUser() {
    const modal = await this.modalController.create({
      component: UsersModalPage,
      componentProps: {
        team: this.team,
      }
    });
    modal.present()
    modal.onDidDismiss().then(() => {
      this.imageConcatString = Utils.generateRandomString();
      let data = this.pageDataService.getData('selectedUsers');
      this.pageDataService.removeData('selectedUsers');
      if (!Utils.isArrayInvalid(data)) {
        for (let user of data) {
          if (this.users.indexOf(user) < 0) {
            this.users.push(user);
            this.filter(this.searchTerm)
          }
        }
      }
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(user: User, leader: boolean = false) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [{
        text: MenuText.REMOVE_FROM_TEAM,
        role: 'destructive',
        handler: () => {
          if (!leader) {
            this.users.splice(this.users.indexOf(user), 1);
            this.filter(this.searchTerm);
          }
          else {
            this.teamLeader = undefined;
          }
        }
      },
      {
        text: MenuText.CANCEL, role: 'cancel',
        handler: () => {
        }
      }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async dismiss() {
    this.modalController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  save() {
    const logHeader = this.logHeader + "save>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(this.teamLeader) || Utils.isVarInvalid(this.teamLeader.getId()) || this.teamLeader.getId() <= 0) {
      this.utils.simpleToastError(ToastText.TEAM_MUST_HAVE_LEADER);
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>LEADER>>" + this.teamLeader);
    }
    if (Utils.isStringInvalid(this.name)) {
      this.utils.simpleToastError(ToastText.INSERT_NAME);
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>NAME>>" + this.name);
    }
    this.team.setLiderId(this.teamLeader.getId());
    this.team.setMembros(this.users);
    this.team.setNome(this.name);
    if (this.modoEdicao) {
      this.api.adminEditTeam(this.team).then((team) => {
        this.imageConcatString = Utils.generateRandomString();
        this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
        this.pageDataService.listUserEvent.emit('newData');
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
      });
    }
    else {
      this.api.createTeam(this.team).then((team) => {
        this.imageConcatString = Utils.generateRandomString();
        this.utils.simpleToast(ToastText.CREATION_SUCCESSFUL);
        this.pageDataService.listUserEvent.emit('newData');
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
      });
    }
    this.dismiss();

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isSaveDisabled(): Boolean {
    return Utils.isVarInvalid(this.teamLeader) || Utils.isVarInvalid(this.teamLeader.getId()) || this.teamLeader.getId() <= 0 || Utils.isStringInvalid(this.name)
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.userList = this.users;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.userList = this.users;
      return;
    }
    this.userList = this.users.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
