import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class ChartItem {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "ChartItem>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Fields */

    private nome: string;
    private dados: Array<number>;
    private labels: Array<string>;

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Getters */

    /**
     * Getter $name
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $dados
     * @return {Array<number>}
     */
    public getDados(): Array<number> {
        return this.dados;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $labels
     * @return {Array<string>}
     */
    public getLabels(): Array<string> {
        return this.labels;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Setters */

    /**
     * Setter $name
     * @param {string} value
     */
    public setNome(value: string) {
        const logHeader = this.logHeader + "setNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + value);
        }
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $dados
     * @param {Array<number>} value
     */
    public setDados(value: Array<number>) {
        const logHeader = this.logHeader + "setDados>>";
        if (Utils.isArrayInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_ARRAY + ">>" + JSON.stringify(value));
        }
        this.dados = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $labels
     * @param {Array<number>} value
     */
    public setLabels(value: Array<string>) {
        const logHeader = this.logHeader + "setLabels>>";
        if (Utils.isArrayInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_ARRAY + ">>" + JSON.stringify(value));
        }
        this.labels = value;
    }
    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(
        nome: string,
        dados: Array<number>,
        labels: Array<string>
    ) {
        const logHeader = this.logHeader + "constructor>>";
        this.nome = nome;
        this.dados = dados;
        this.labels = labels;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): ChartItem {
        const logHeader = "ChartItem>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let nome = json.nome;
        let dados = json.dados;
        let labels = json.labels;
        return new ChartItem(nome, dados, labels);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//


}