import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';
import { MaterialModule } from './material.module';

const routes: Routes = [

  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', loadChildren: './public/login/login.module#LoginPageModule' },
  { path: 'recovery', loadChildren: './public/recovery/recovery.module#RecoveryPageModule' },
  { path: 'register', loadChildren: './public/register/register.module#RegisterPageModule' },
  { path: 'admin', canActivate: [AuthGuard], loadChildren: './admin/admin-routing.module#AdminRoutingModule' },
  { path: '', loadChildren: './users/home/home/tabs.module#TabsPageModule' },
  { path: '', canActivate: [AuthGuard], loadChildren: './admin/areas/areas.module#AreasPageModule' },  
  { path: '', canActivate: [AuthGuard], loadChildren: './admin/pontos/pontos.module#PontosPageModule' },
  { path: 'dashboard', canActivate: [AuthGuard], loadChildren: './admin/dashboard/dashboard.module#DashboardPageModule' },
  { path: 'members', canActivate: [AuthGuard], loadChildren: './admin/members/members.module#MembersPageModule' },
  { path: 'users', loadChildren: './admin/members/new/new.module#RegisterUserPageModule' },
  { path: 'teams', loadChildren: './users/teams/teams.module#TeamsPageModule' },
  { path: 'team', canActivate: [AuthGuard], loadChildren: './admin/team/team.module#TeamPageModule' },
  { path: 'prize', canActivate: [AuthGuard], loadChildren: './admin/prize/prize.module#PrizePageModule' },
  { path: 'rules', loadChildren: './users/rules/rules.module#RulesPageModule' },
  { path: 'pending', loadChildren: './users/teams/pending/pending.module#PendingPageModule' },
  { path: 'exchange', loadChildren: './users/exchange/exchange.module#ExchangePageModule' },
  { path: 'email-confirm', loadChildren: './users/email-confirm/email-confirm.module#EmailConfirmPageModule' },
  { path: 'profile', loadChildren: './users/profile/profile.module#ProfilePageModule' },
  { path: 'communication', loadChildren: './users/communication/communication.module#CommunicationPageModule' },
  { path: 'notification', loadChildren: './users/notification/notification.module#NotificationPageModule' },
  { path: 'tutorial', loadChildren: './users/tutorial/tutorial.module#TutorialPageModule' },
  { path: 'new', canActivate: [AuthGuard], loadChildren: './admin/areas/fabrica/new/new.module#NewPageModule' },
  { path: 'fabrica-menu', canActivate: [AuthGuard], loadChildren: './admin/areas/fabrica/fabrica-menu/fabrica-menu.module#FabricaMenuPageModule' },
  { path: 'linha-menu', canActivate: [AuthGuard], loadChildren: './admin/areas/linha/linha-menu/linha-menu.module#LinhaMenuPageModule' },
  { path: 'new', canActivate: [AuthGuard], loadChildren: './admin/areas/linha/new/new.module#NewPageModule' },
  { path: 'new', canActivate: [AuthGuard], loadChildren: './admin/areas/secao/new/new.module#NewPageModule' },
  { path: 'secao-menu', canActivate: [AuthGuard], loadChildren: './admin/areas/secao/secao-menu/secao-menu.module#SecaoMenuPageModule' },
  { path: 'zeramento', canActivate: [AuthGuard], loadChildren: './admin/pontos/users/zeramento/zeramento.module#ZeramentoPageModule' },
  { path: 'pontos-menu', canActivate: [AuthGuard], loadChildren: './admin/pontos/pontos-menu/pontos-menu.module#PontosMenuPageModule' }
];

@NgModule({
  imports: [MaterialModule,
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules, useHash: true })
  ],
  exports: [RouterModule]
})

export class AppRoutingModule { }
