import { Component } from '@angular/core';
import { Platform, ToastController, LoadingController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AuthenticationService } from './services/authentication.service';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { MenuController } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { ToastText, MenuText } from 'src/commons/Constants';
import { Utils } from 'src/commons/Utils';
import { User } from 'src/models/User';
import { PageDataService } from 'src/providers/page-data/page-data.service';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class AppComponent {

  //-------------------------------------------------------------------------------------------------------------------------------//

  loaderToShow: any;

  rootPage: any;

  pages = [
    {
      title: 'Dashboard',
      url: '/dashboard',
      icon: 'analytics'
    },
    {
      title: 'Regulamento',
      url: '/rules',
      icon: 'star'
    },
    {
      title: 'Meu Perfil',
      url: '/profile',
      icon: 'cog'
    },
    {
      title: 'Pontos',
      url: '/home/extrato',
      icon: 'ribbon'
    },
    {
      title: 'Minha Equipe',
      url: '/teams',
      icon: 'trophy'
    },
    {
      title: 'Troca de Prmios',
      url: '/exchange',
      icon: 'cart'
    },
    {
      title: 'Comunicao',
      url: '/communication',
      icon: 'mail'
    }
    ,
    {
      title: 'Notificao',
      url: '/notification',
      icon: 'alert'
    }, {
      title: 'Tutorial',
      url: '/tutorial',
      icon: 'help-circle'
    }

  ];
  toastInstance: Promise<void>;
  toast: Promise<void>;

  imageConcatString = Utils.generateRandomString();


  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private authenticationService: AuthenticationService,
    private router: Router,
    private authService: AuthenticationService,
    public toastController: ToastController,
    public loadingController: LoadingController,
    public api: BackendApiProvider,
    public utils: Utils,
    public pageDataService: PageDataService
  ) {
    this.initializeApp();
    this.pageDataService.imageChangeEvent.subscribe(() => {
      this.imageConcatString = Utils.generateRandomString();
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async initializeApp() {
    this.platform.ready().then(() => {
      this.authService.isAuthenticated().subscribe(a => {
        if (!a) {
          this.statusBar.styleDefault();
          this.splashScreen.hide();
          this.router.navigate(['/login']);
        } else {
          this.authenticationService.isAdmin().subscribe(isadm => {
            if (this.authenticationService.loggedUser.isFinalizado()) {
              if (isadm)
                this.router.navigate(['admin', 'dashboard']);
              else
                this.router.navigate(['home', 'pontos']);
            }
            else {
              this.router.navigate(['email-confirm'])
            }

            this.statusBar.styleDefault();
            this.splashScreen.hide();
          });
        }
      });

    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  ngOnInit() {
    this.router.events.subscribe((event: RouterEvent) => {
      if (event instanceof NavigationEnd) {
        this.pages.map(p => {
          return p['active'] = (event.url === p.url);
        });
      }
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  logout() {
    this.authService.logout();
    this.toast = this.toastController.create({
      message: ToastText.LOGOUT_SUCCESSFUL,
      showCloseButton: true,
      position: 'bottom',
      duration: 3000,
      closeButtonText: MenuText.OK,
      animated: true,
      cssClass: "toast-mdlz"
    }).then((toastData) => {
      toastData.present();
      this.authService.logout();
      this.router.navigate(['login']);
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getUser() {
    return !Utils.isVarInvalid(this.authService.loggedUser) ? this.authService.loggedUser : new User();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}