import { Component, OnInit } from '@angular/core';
import { PopoverController, NavParams } from '@ionic/angular';
import { NewPageF } from '../new/new.page';
import { Factory } from 'src/models/Factory';

@Component({
  selector: 'app-fabrica-menu',
  templateUrl: './fabrica-menu.page.html',
  styleUrls: ['./fabrica-menu.page.scss'],
})
export class FabricaMenuPage implements OnInit {

  private factory: Factory;

  constructor(
    public popoverController: PopoverController,
    private navParams: NavParams
  ) {
    this.factory = this.navParams.get('factory');
  }

  ngOnInit() {
  }

  async newFabrica(ev: any) {
    this.DismissClick()
    const popover = await this.popoverController.create({
      component: NewPageF,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class',
      componentProps: {
        factory: this.factory
      }
    });

    return await popover.present();

  }

  async DismissClick() {
    await this.popoverController.dismiss();
  }


}
