import { Component, OnInit } from '@angular/core';
import { ModalController, PopoverController } from '@ionic/angular';
import { NewPage } from '../new/new.page';

@Component({
  selector: 'app-team-menu',
  templateUrl: './team-menu.page.html',
  styleUrls: ['./team-menu.page.scss'],
})
export class TeamMenuPage{

  constructor(private popoverController: PopoverController, public modalController: ModalController) { }

  async newTeam() {
    const modal = await this.modalController.create({
      component: NewPage
    });
    modal.present().then(() => this.popoverController.dismiss());
  }

}
