import { Injectable, EventEmitter } from '@angular/core';
import { Utils } from 'src/commons/Utils';

Injectable({
  providedIn: 'root'
})

interface IDictionary {
  [index: string]: string;
}

export class PageDataService {

  private readonly logHeader = "PageDataService>>";

  private data: IDictionary = {};

  public listUserEvent : EventEmitter<any> = new EventEmitter;

  public imageChangeEvent : EventEmitter<any> = new EventEmitter;

  pushUsers(list){
    this.listUserEvent.emit(list);
  }

  constructor() {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
  }

  setData(id: string, data: any):void {
    const logHeader=this.logHeader+"setData>>";
    Utils.log(logHeader+">>ID>>"+id+">>DATA>>"+JSON.stringify(data));
    this.data[id] = data;
  }

  getData(id:string):any{
    const logHeader=this.logHeader+"getData>>";
    Utils.log(logHeader);
    Utils.log(logHeader+">>ID>>"+id);
    let returnValue=this.data[id];
    return returnValue;
  }

  removeData(id:string):void{
    const logHeader=this.logHeader+"getData>>";
    Utils.log(logHeader);
    Utils.log(logHeader+">>ID>>"+id);
    delete this.data[id];
  }

}
