import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';
import { Factory } from './Factory';

export class Line {
    
    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Line>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    private id: number;
    private secaoId: number;
    private nome: string;

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $secaoId
     * @return {number}
     */
    public getSecaoId(): number {
        return this.secaoId;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        const logHeader = this.logHeader + "setId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + value);
        }
        this.id = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $secaoId
     * @param {number} value
     */
    public setSecaoId(value: number) {
        const logHeader = this.logHeader + "setSecaoId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + value);
        }
        this.secaoId = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        const logHeader = this.logHeader + "setNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + value);
        }
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public constructor(secaoId: number, nome: string,id: number=undefined) {
        const logHeader = this.logHeader + "castFromJSON>>";
        Utils.log(logHeader);
        // if (Utils.isVarInvalid(secaoId) || secaoId <= 0) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>SECAO>>" + secaoId);
        // }
        // if (Utils.isStringInvalid(nome)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_STRING + "NOME>>" + nome);
        // }
        this.id = id;
        this.secaoId = secaoId;
        this.nome = nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Line {
        const logHeader = "Line>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let id = json.id;
        let secaoId = json.secaoId;
        let nome = json.nome;
        return new Line(secaoId, nome, id);
    }
    
    //-------------------------------------------------------------------------------------------------------------------------------//

}