import { Utils } from 'src/commons/Utils';
import { ErrorType, ErrorMessage } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Schooling {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Schooling>>"

    //-------------------------------------------------------------------------------------------------------------------------------//

    public description: string;
    public id: number;
    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(id: number = undefined, description: string = undefined) {
        const logHeader = this.logHeader + "constructor>>";
        if (Utils.isVarInvalid(id) || id <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + id);
        }
        if (Utils.isStringInvalid(description)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + description);
        }
        this.id = id;
        this.description = description;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}