import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class ExtractItem {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "ExtractItem>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Fields */

    private data: Date;
    private valor: number;
    private nome: string;
    private tipo: string;


    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Getters */

    /**
     * Getter $data
     * @return {Date}
     */
    public getData(): Date {
        return this.data;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $valor
     * @return {number}
     */
    public getValor(): number {

        return this.valor;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $tipo
     * @return {string}
     */
    public getTipo(): string {
        return this.tipo;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Setters */

    /**
     * Setter $data
     * @param {Date} value
     */
    public setData(value: Date) {
        this.data = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $valor
     * @param {number} value
     */
    public setValor(value: number) {
        this.valor = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $tipo
     * @param {string} value
     */
    public setTipo(value: string) {
        this.tipo = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(data: Date, valor: number, nome: string) {
        const logHeader = this.logHeader + "constructor>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(data)) {
            throw new Error(logHeader + ErrorMessage.INVALID_DATE + ">>" + data);
        }
        if (Utils.isVarInvalid(valor)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + valor);
        }
        if (Utils.isStringInvalid(nome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + nome);
        }
        this.data = data;
        this.valor = valor;
        this.nome = nome;
        if (this.nome.includes('Resgate')) {
            this.nome = this.nome.replace('Resgate prmio', '');
            this.tipo = 'Resgate de Prmio'
        }
        else {
            this.tipo = 'Atividade';
        }
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any) {
        const logHeader = "ExtractItem>>castFromJSON>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        return new ExtractItem(
            new Date(json.data),
            json.valor,
            json.descricao,
        );
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}