import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, ActionSheetController, Platform } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { Reward } from 'src/models/Reward';
import { ToastText, ErrorMessage, MenuText, ErrorType } from 'src/commons/Constants';
import { Activity } from 'src/models/Activity';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { PictureSourceType } from '@ionic-native/camera/ngx';

@Component({
  selector: 'app-new-reward',
  templateUrl: './new-reward.page.html',
  styleUrls: ['./new-reward.page.scss'],
})
export class NewRewardPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "NewRewardPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private modoEdicao: boolean = false;
  private reward: Reward;
  public active: boolean;
  public name: string;
  public cost: number;
  public description: string;
  public quantity: any;
  public photo: string;
  private imageData;
  private imagePath;
  private uploadImage: boolean = false;
  private imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    public modalController: ModalController,
    private navParams: NavParams,
    private api: BackendApiProvider,
    private utils: Utils,
    private pageDataService: PageDataService,
    private actionSheetController: ActionSheetController,
    private platform: Platform
  ) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    let reward = this.navParams.get('reward') as Reward;
    this.modoEdicao = !Utils.isVarInvalid(reward);
    if (this.modoEdicao) {
      this.reward = reward;
      this.photo = this.reward.getFoto() + '?' + Utils.generateRandomString();
      this.active = this.reward.isAtivo();
      this.name = this.reward.getNome();
      this.cost = this.reward.getValor();
      this.description = this.reward.getDescricao();
      this.quantity = this.reward.getQuantidade();
    }
    else {
      this.reward = new Reward();
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

  save() {
    const logHeader = this.logHeader + "save>>";
    Utils.log(logHeader);
    if (this.uploadImage && this.modoEdicao) {
      const imageBlob = Utils.getImageBlob(this.imageData);
      this.api.uploadRewardImage(this.reward.getId(), imageBlob).then(() => {
        this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
        this.uploadImage = false;
        this.pageDataService.imageChangeEvent.emit();
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.IMAGE_UPLOAD_FAILED);
        throw error;
      }).finally(() => this.dismiss());
      return;
    }
    else {
      if (Utils.isVarInvalid(this.active)) {
        this.active = false;
      }
      if (Utils.isStringInvalid(this.name)) {
        this.utils.simpleToastError(ToastText.INSERT_NAME);
        throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>NAME>>" + this.name);
      }
      if (Utils.isVarInvalid(this.cost)) {
        this.utils.simpleToastError(ToastText.INSERT_VALUE);
        throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>COST>>" + this.cost);
      }
      if (Utils.isStringInvalid(this.description)) {
        this.utils.simpleToastError(ToastText.INSERT_DESCRIPTION);
        throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>DESCRIPTION>>" + this.description);
      }
      if (Utils.isStringInvalid(this.quantity)) {
        this.quantity = null;
      }
      this.reward.setAtivo(this.active)
      this.reward.setNome(this.name);
      this.reward.setValor(this.cost);
      this.reward.setDescricao(this.description);
      this.reward.setQuantidade(+this.quantity);
      if (this.modoEdicao) {
        this.api.editReward(this.reward).then((reward) => {
          this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
          this.pageDataService.setData('rewardData', reward);
          this.pageDataService.setData('rewardEdit', true);
          this.pageDataService.listUserEvent.emit('data');
        }).catch((error) => {
          let message = JSON.parse(error._body).message as string;
          if (message.includes(ErrorType.DUPLICATED)) {
            this.utils.simpleToastError(ToastText.ALREADY_EXISTS);
          }
          else {
            this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG)
          }
        }).finally(() => this.dismiss());
      }
      else {
        this.api.createReward(this.reward).then((reward: Reward) => {
          this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
          this.pageDataService.setData('rewardData', reward);
          this.pageDataService.listUserEvent.emit('data');
          if (this.uploadImage) {
            const imageBlob = Utils.getImageBlob(this.imageData);
            this.api.uploadRewardImage(reward.getId(), imageBlob).then(() => {
              this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
              this.uploadImage = false;
            }).catch((error) => {
              this.utils.simpleToastError(error + '\n' + ToastText.IMAGE_UPLOAD_FAILED);
              throw error;
            });
          }
        }).catch((error) => {
          let message = JSON.parse(error._body).message as string;
          if (message.includes(ErrorType.DUPLICATED)) {
            this.utils.simpleToastError(ToastText.ALREADY_EXISTS);
          }
          else {
            this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG)
          }
        }).finally(() => this.dismiss());
      }
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

  isSaveDisabled() {
    return Utils.isStringInvalid(this.name) || Utils.isVarInvalid(this.cost) || Utils.isStringInvalid(this.description)
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

  async selectImage() {
    if (!(this.platform.is('desktop') || this.platform.is('mobileweb'))) {
      const actionSheet = await this.actionSheetController.create({
        header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
        buttons: [{
          text: MenuText.LOAD_FILE,
          handler: () => {
            this.takePicture(PictureSourceType.PHOTOLIBRARY)
          }
        },
        {
          text: MenuText.TAKE_PICTURE,
          handler: () => {
            this.takePicture(PictureSourceType.CAMERA)
          }
        },
        {
          text: MenuText.CANCEL,
          role: 'cancel'
        }
        ]
      });
      await actionSheet.present();
    }
    else {
      this.utils.simpleToastError(ToastText.FEATURE_UNAVAILABLE_FOR_WEB);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  takePicture(sourceType: PictureSourceType) {
    this.utils.takePicture(sourceType).then((imageData) => {
      this.utils.cropPicture(imageData).then((croppedImage) => {
        this.photo = croppedImage;
        let image: string = croppedImage as string;
        this.imageData = image.substring(image.indexOf("base64,") + 7, image.length);
        this.uploadImage = true;
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG)
      });
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG)
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

}
