import { ErrorMessage, Constants } from 'src/commons/Constants';
import { Utils } from 'src/commons/Utils';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Message {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Message>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    private id: number;
    private message: string;
    private data: Date;
    private lida: boolean;
    private fromUser: number;
    private fromUsernome: string;
    private toUser: number;
    private toUsernome: string;
    private fromUserFoto: string;
    private fromUserFabricaNome: string;


    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Getters */

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $message
     * @return {string}
     */
    public getMessage(): string {
        return this.message;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $data
     * @return {Date}
     */
    public getData(): Date {
        return this.data;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $lida
     * @return {boolean}
     */
    public getLida(): boolean {
        return this.lida;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $fromUser
     * @return {number}
     */
    public getFromUser(): number {
        return this.fromUser;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $fromUserNome
     * @return {string}
     */
    public getFromUserNome(): string {
        return this.fromUsernome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $fromUserFoto
     * @return {string}
     */
    public getFromUserFoto(): string {
        return this.fromUserFoto;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $toUser
     * @return {number}
     */
    public getToUser(): number {
        return this.toUser;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $toUserNome
     * @return {string}
     */
    public getToUserNome(): string {
        return this.toUsernome;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Setters */

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        const logHeader = this.logHeader + "setId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.id = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $message
     * @param {string} value
     */
    public setMessage(value: string) {
        const logHeader = this.logHeader + "setMessage>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.message = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $data
     * @param {Date} value
     */
    public setData(value: Date) {
        const logHeader = this.logHeader + "setData>>";
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.data = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $lida
     * @param {boolean} value
     */
    public setLida(value: boolean) {
        const logHeader = this.logHeader + "setLida>>";
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.lida = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $fromUser
     * @param {number} value
     */
    public setFromUser(value: number) {
        const logHeader = this.logHeader + "setFromUser>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.fromUser = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $fromUserNome
     * @param {string} value
     */
    public setFromUserNome(value: string) {
        const logHeader = this.logHeader + "setFromUserNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.fromUsernome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $toUser
     * @param {number} value
     */
    public setToUser(value: number) {
        const logHeader = this.logHeader + "setFromUser>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.toUser = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $toUserNome
     * @param {string} value
     */
    public setToUserNome(value: string) {
        const logHeader = this.logHeader + "setToUserNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE)
        }
        this.toUsernome = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//


    constructor(
        id: number = undefined,
        message: string = undefined,
        data: Date = undefined,
        lida: boolean = undefined,
        fromUser: number = undefined,
        fromUsernome: string = undefined,
        toUser: number = undefined,
        toUsernome: string = undefined,
        fromUserFoto: string = undefined,
    ) {
        const logHeader = this.logHeader + "constructor>>";
        if (Utils.isVarInvalid(id) || id <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>ID>>" + id);
        }
        if (Utils.isStringInvalid(message)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>MESSAGE>>" + message);
        }
        if (Utils.isVarInvalid(data)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>DATA>>" + data);
        }
        if (Utils.isVarInvalid(lida)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>LIDA>>" + lida);
        }
        if (Utils.isVarInvalid(fromUser) || fromUser <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>FROM_USER>>" + fromUser);
        }
        if (Utils.isStringInvalid(fromUsernome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>FROM_USER_NOME>>" + fromUsernome);
        }
        if (Utils.isVarInvalid(toUser) || toUser <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>TO_USER>>" + toUser);
        }
        if (Utils.isStringInvalid(toUsernome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>TO_USER_NOME>>" + toUsernome);
        }
        if (Utils.isStringInvalid(fromUserFoto)) {
            fromUserFoto = Constants.DEFAULT_USER_PHOTO;
        }
        this.id = id;
        this.message = message;
        this.data = data;
        this.lida = lida;
        this.fromUser = fromUser;
        this.fromUsernome = fromUsernome;
        this.toUser = toUser;
        this.toUsernome = toUsernome;
        this.fromUserFoto = fromUserFoto;
    }


    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Message {
        const logHeader = "User>>castFromJSON>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        return new Message(
            json.id,
            json.message,
            new Date(json.data),
            json.lida,
            json.fromUser,
            json.fromUsernome,
            json.toUser,
            json.toUsernome,
            json.fromUserFoto
        );
    }

}