import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, PopoverController } from '@ionic/angular';
import { NewRewardPage } from '../new-reward/new-reward.page';

@Component({
  selector: 'app-prize-menu',
  templateUrl: './prize-menu.component.html',
  styleUrls: ['./prize-menu.component.scss'],
})
export class PrizeMenuComponent implements OnInit {

  constructor(private modalController: ModalController,
    private popoverController: PopoverController) { }

  ngOnInit() { }

  async newPrize() {
    const modal = await this.modalController.create({
      component: NewRewardPage
    });
    modal.present().then(()=>this.popoverController.dismiss());
  }
}
