import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Activity {

    private readonly logHeader = "Activity>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Fields */

    private id: number;
    private valor: number;
    private nome: string;
    private pilar: string;

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Getters */

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $valor
     * @return {number}
     */
    public getValor(): number {
        return this.valor;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $pilar
     * @return {string}
     */
    public getPilar(): string {
        return this.pilar;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Setters */

    /**
     * Setter $valor
     * @param {number} value
     */
    public setValor(value: number) {
        this.valor = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $motivo
     * @param {string} value
     */
    public setPilar(value: string) {
        this.pilar = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(valor: number, nome: string, pilar: string, id: number = undefined) {
        const logHeader = this.logHeader + "constructor>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(valor)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>VALOR>>" + valor);
        }
        if (Utils.isStringInvalid(nome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>MOTIVO>>" + nome);
        }
        this.id = id;
        this.valor = valor;
        this.nome = nome;
        this.pilar = pilar;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any) {
        const logHeader = "Activity>>castFromJSON>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        return new Activity(
            json.valor,
            json.nome,
            json.pilar,
            json.id
        );
    }

}