import { Component, OnInit } from '@angular/core';
import { PopoverController, NavParams } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Activity } from 'src/models/Activity';
import { Utils } from 'src/commons/Utils';
import { ErrorMessage, ToastText } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-pontos-remove',
  templateUrl: './pontos-remove.page.html',
  styleUrls: ['./pontos-remove.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class PontosRemovePage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "PontosRemovePage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private users: Array<number> = new Array<number>();
  public name: string;
  public value: number;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public popoverController: PopoverController, private navParams: NavParams, private api: BackendApiProvider, private utils:Utils) {
    const logHeader = this.logHeader + "constructor>>";
    this.users = this.navParams.get('users');
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async RemovePoints() {
    const logHeader = this.logHeader + "RemovePoints>>";
    if (Utils.isStringInvalid(this.name)) {
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + this.name)
    }
    if (Utils.isVarInvalid(this.value) || this.value <= 0) {
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + this.value)
    }
    // TODO REMOO DE PONTOS
    // this.api.addPoints(this.users, new Activity(this.value,this.name)).then(()=>{
    //   this.utils.simpleToast(ToastText.POINTS_REMOVED);
    // }).catch((error)=>{
    //   this.utils.simpleToastError(ToastText.SOMETHING_WENT_WRONG);
    // }).finally(()=>{
    //   this.DismissClick();
    // })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
