import { Utils } from 'src/commons/Utils';
import { ErrorMessage, Constants } from 'src/commons/Constants';

export class Invitation {

    private readonly logHeader = "Invitation>>";

    private id: number;
    private equipeId: number;
    private userId: number;
    private userNome: string;
    private fabricaNome: string;
    private secaoNome: string;
    private linhaNome: string;
    private foto: string;

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    /**
     * Getter $equipeId
     * @return {number}
     */
    public getEquipeId(): number {
        return this.equipeId;
    }

    /**
     * Getter $userId
     * @return {number}
     */
    public getUserId(): number {
        return this.userId;
    }

    /**
     * Getter $userNome
     * @return {string}
     */
    public getUserNome(): string {
        return this.userNome;
    }

    /**
     * Getter $fabricaNome
     * @return {string}
     */
    public getFabricarNome(): string {
        return this.fabricaNome;
    }

    /**
     * Getter $secaoNome
     * @return {string}
     */
    public getSecaoNome(): string {
        return this.secaoNome;
    }

    /**
     * Getter $linhaNome
     * @return {string}
     */
    public getLinhaNome(): string {
        return this.linhaNome;
    }

    /**
     * Getter $foto
     * @return {string}
     */
    public getFoto(): string {
        return this.foto;
    }

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        this.id = value;
    }

    /**
     * Setter $equipeId
     * @param {number} value
     */
    public setEquipeId(value: number) {
        this.equipeId = value;
    }

    /**
     * Setter $userId
     * @param {number} value
     */
    public setUserId(value: number) {
        this.userId = value;
    }

    /**
     * Setter $userNome
     * @param {string} value
     */
    public setUserNome(value: string) {
        this.userNome = value;
    }

    /**
     * Setter $fabricaNome
     * @param {string} value
     */
    public setFabricaNome(value: string) {
        this.fabricaNome = value;
    }
    /**
     * Setter $secaoNome
     * @param {string} value
     */
    public setSecaoNome(value: string) {
        this.secaoNome = value;
    }
    /**
     * Setter $linhaNome
     * @param {string} value
     */
    public setLinhaNome(value: string) {
        this.linhaNome = value;
    }
    /**
     * Setter $foto
     * @param {string} value
     */
    public setFoto(value: string) {
        this.foto = value;
    }


    constructor(equipeId: number = undefined,
        id: number = undefined,
        userId: number = undefined,
        userNome: string = undefined,
        fabricaNome: string = undefined,
        secaoNome: string = undefined,
        linhaNome: string = undefined,
        foto: string = undefined) {
        this.equipeId = equipeId;
        this.id = id;
        this.userId = userId;
        this.userNome = userNome;
        this.fabricaNome = fabricaNome;
        this.secaoNome = secaoNome;
        this.linhaNome = linhaNome;
        if (Utils.isStringInvalid(foto)) {
            foto = Constants.DEFAULT_USER_PHOTO;
        }
        this.foto = "https://mdlzplusstorage.blob.core.windows.net/user/" + id;
    }

    static castFromJSON(input: any): Invitation {
        const logHeader = "Invitation>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }

        let equipeId = json.equipeId;
        let id = json.id;
        let userId = json.userId;
        let userNome = json.userNome;
        let fabricaNome = json.fabricaNome;
        let secaoNome = json.secaoNome;
        let linhaNome = json.linhaNome;
        let foto = json.foto;
        return new Invitation(equipeId, id, userId, userNome, fabricaNome, secaoNome, linhaNome, foto);
    }

}