    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region Utils */

    private getHeaders(needsToken: boolean = true): Observable<any> {
        return new Observable(obs => {
            let headers = new Headers;
            headers.append('Content-Type', 'application/json');
            if (needsToken) {
                this.authService.getToken().subscribe(token => {
                    if (token)
                        headers.append(`token`, token);

                    return obs.next(headers);
                });
            }
            else {
                return obs.next(headers);
            }
        });
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region HTTP Methods */

    private put(endpoint: Endpoint, data: any, needsToken: boolean = true): Promise<any> {
        const logHeader = this.logHeader + "put>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(endpoint as string)) {
            throw new Error(logHeader + ErrorMessage.INVALID_ENDPOINT + endpoint);
        }
        if (Utils.isVarInvalid(this.loggedUser)) {
            this.loggedUser = this.authService.loggedUser;
        }
        this.loader.present();
        return new Promise((resolve, reject) => {
            this.getHeaders(needsToken).subscribe(headers => {
                const options = {
                    headers: headers
                };
                if (!Utils.isVarInvalid(data) && data.constructor.name === "FormData") {
                    options.headers.delete('Content-Type');
                }
                this.http.put(this.url + '/' + endpoint, data, options).subscribe(
                    (result: any) => {
                        resolve(result);
                    },
                    (error) => {
                        if (error.status == 403 || error.status == 401) {
                            this.utils.simpleToastError(ToastText.LOGIN_EXPIRED);
                            this.setLoggedUser(undefined);
                            this.router.navigate([`/login`]);
                        }
                        else if (error.status == 0) {
                            this.utils.simpleToastError(ToastText.COMMUNICATION_FAILURE);
                        }
                        else {
                            reject(error);
                        }
                    }, () => this.loader.dismiss());
            });
        });
    }

