    //-------------------------------------------------------------------------------------------------------------------------------//

    public login(login: string, password: string) {
        const logHeader = this.logHeader + "login>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(login)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>LOGIN>>" + login);
        }
        if (Utils.isStringInvalid(password)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>PASSWORD>>" + password);
        }
        if (Constants.TEST && Constants.ISOLATED) {
            return new Observable((observer) => {
                let user = new User(true, true, login, true, 1, 1, "123456", "Nome do usurio logado", "senha", "token", 0, null, "Linha", "Seo", "Fbrica", 1, "Equipe", "987456312654", "Ensino superior completo");
                observer.next({ status: 200, _body: user });
                observer.complete();
            })
        }
        login = login.toLowerCase();
        let headers = new Headers;
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization', 'Basic ' + window.btoa(login + ':' + password));
        const options = {
            headers: headers
        };

        return this.http.get(this.url + '/' + Endpoint.LOGIN, options);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public tokenLogin() {
        const logHeader = this.logHeader + "tokenLogin>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(this.authService.loggedUser)) {
            throw new Error(logHeader + ErrorMessage.INVALID_USER + ">>" + this.authService.loggedUser);
        }
        return new Promise((resolve, reject) => {
            this.get(Endpoint.LOGIN).then((response: any) => {
                let user = User.castFromJSON(response._body);
                this.authService.loggedUser = user;
                this.authService.setAuthObject(user);
                this.setLoggedUser(user);
                resolve(user);
            }).catch((error) => reject(error));
        })

    }

