    public async getActivities(page: number) {
        const logHeader = this.logHeader + "getActivities>>";
        Utils.log(logHeader);
        // if (Utils.isVarInvalid(page) || page <= 0) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>PAGE>>" + page);
        // }
        if (Constants.TEST && Constants.ISOLATED) {
            return new Promise((resolve, reject) => {
                resolve([
                    new Activity(150, "Motivo 1", "Liderana"),
                    new Activity(151, "Motivo 2", "SSMA"),
                    new Activity(152, "Motivo 3", "MF"),
                    new Activity(153, "Motivo 4", "IIM"),
                    new Activity(154, "Motivo 5", "MP"),
                    new Activity(155, "Motivo 6", "MA"),
                    new Activity(156, "Motivo 7", "QM"),
                    new Activity(157, "Motivo 8", "WPI")
                ]);
            })
        }
        else {
            let activities: Array<Activity> = new Array<Activity>();
            if (Utils.isVarInvalid(page) || page <= 0) {
                let totalPages = 999;
                let currentPage = 1;
                while (currentPage <= totalPages) {
                    let response = JSON.parse(((await this.post(Endpoint.GET_ACTIVITIES,
                        {
                            "pageNumber": currentPage,
                            "pageSize": 999,
                            "totalElements": 999,
                            "totalPages": 999
                        }
                    )) as any)._body);
                    totalPages = response.totalPages;
                    for (let activity of response.list) {
                        activities.push(Activity.castFromJSON(activity))
                    }
                    currentPage++
                }
                return { list: activities, totalPages: 1 };
            }
            else {
                let response = JSON.parse(((await this.post(Endpoint.GET_ACTIVITIES,
                    {
                        "pageNumber": page,
                        "pageSize": this.pageSize
                    }
                )) as any)._body);
                const totalPages = response.totalPages;
                for (let activity of response.list) {
                    activities.push(Activity.castFromJSON(activity))
                }
                return { list: activities, totalPages: totalPages };
            }

        }
    }

