    public async addPoints(users: Array<number>, activity: Activity) {
        const logHeader = this.logHeader + "addPoints>>";
        Utils.log(logHeader);
        if (Utils.isArrayInvalid(users)) {
            throw new Error(logHeader + ErrorMessage.INVALID_ARRAY + ">>USERS>>" + users);
        }
        else {
            for (let user of users) {
                if (Utils.isVarInvalid(user) || user <= 0) {
                    throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>USER>>" + user);
                }
            }
        }
        if (Utils.isVarInvalid(activity)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>ACTIVITY>>" + activity);
        }

        if (Constants.TEST && Constants.ISOLATED) {
            return new Promise((resolve, reject) => {
                resolve();
            })
        }
        else {
            return this.post(Endpoint.ADD_POINTS, { atividadeId: activity.getId(), users: users });
        }
    }

