import { Component, OnInit } from '@angular/core';
import { PopoverController, NavParams } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { CartItem } from 'src/models/CartItem';
import { isArray } from 'util';
import { ToastText, ErrorMessage } from 'src/commons/Constants';
import { Reward } from 'src/models/Reward';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-cart',
  templateUrl: './cart.page.html',
  styleUrls: ['./cart.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class CartPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "CartPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public shoppingCart: Array<CartItem> = new Array<CartItem>();
  public points: number = 0;


  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    private navParams: NavParams) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getPoints();
    let cart = this.navParams.get('cart');
    if (!Utils.isArrayInvalid(cart)) {
      this.shoppingCart = cart;
    }
    else {
      this.DismissClick();
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  incrementQty(item: CartItem) {
    const logHeader = this.logHeader + "incrementQty>>";
    if (item.getPremio().getQuantidade() > item.getQuantidade() || Utils.isVarInvalid(item.getPremio().getQuantidade())) {
      item.addQuantidade(1);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  decrementQty(item: CartItem) {
    const logHeader = this.logHeader + "decrementQty>>";
    if (item.getQuantidade() > 0) {
      item.addQuantidade(-1);
    }
    if (item.getQuantidade() == 0) {
      this.shoppingCart.splice(this.shoppingCart.indexOf(this.shoppingCart.filter(n => n.getPremio().getId() == item.getPremio().getId())[0]), 1);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getPoints() {
    this.api.getPoints().then((points: number) => {
      this.points = points;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }


  //-------------------------------------------------------------------------------------------------------------------------------//


  ZerarPontos() {
    const auxCart = JSON.parse(JSON.stringify(this.shoppingCart));
    for (let auxItem of auxCart) {
      let item = CartItem.castFromJSON(auxItem);
      item.setQuantidade(0);
      this.shoppingCart.splice(this.shoppingCart.indexOf(this.shoppingCart.filter(n => n.getPremio().getId() == item.getPremio().getId())[0]), 1);
    }
    this.popoverController.dismiss();
  }


  //-------------------------------------------------------------------------------------------------------------------------------//

  getTotal() {
    let cost = 0;
    for (let item of this.shoppingCart) {
      cost += item.getPremio().getValor() * item.getQuantidade();
    }
    return cost;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  save() {
    const logHeader = this.logHeader + "save>>";
    if (this.points - this.getTotal() < 0) {
      this.utils.simpleToastError(ToastText.INSUFICIENT_FUNDS);
      throw new Error(logHeader + ErrorMessage.INSUFICIENT_FUNDS);
    }
    if (Utils.isArrayInvalid(this.shoppingCart)) {
      this.popoverController.dismiss();
      throw new Error(logHeader + ErrorMessage.INVALID_ARRAY + this.shoppingCart);
    }
    this.api.exchange(this.shoppingCart).then(() => {
      this.utils.simpleToast(ToastText.EXCHANGE_SUCCESSFUL);
      this.ZerarPontos();
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.EXCHANGE_FAILED));
  }

}
