import { AuthenticationService } from './../../services/authentication.service';
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MenuController, NavController, IonSlides } from '@ionic/angular';
import { Chart } from 'chart.js';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { AlertTitle, AlertText, MenuText, ToastText } from 'src/commons/Constants';
import { ChartItem } from 'src/models/ChartItem';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class DashboardPage implements OnInit {

  //-------------------------------------------------------------------------------------------------------------------------------//

  @ViewChild('slides') slides: IonSlides;
  @ViewChild("barCanvas") barCanvas: ElementRef;
  @ViewChild("pieCanvas") pieCanvas: ElementRef;
  @ViewChild("lineBarCanvas") lineBarCanvas: ElementRef;
  @ViewChild("lineCanvas") lineCanvas: ElementRef;

  //-------------------------------------------------------------------------------------------------------------------------------//

  sliderTwo: any;
  slideOptsTwo = {
    initialSlide: 0,
    slidesPerView: 1
  }
  private ages = 0;
  private item_qty: any;
  sliderThree: any;
  private atividadesPontuadas: Chart;
  private premiosResgatados: Chart;
  private pontosPilar: Chart;
  private pontosUsuarios: Chart;
  public linhas: ChartItem;
  public usuarios: Array<any>;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private authService: AuthenticationService,
    public menu: MenuController,
    private nav: NavController,
    private api: BackendApiProvider,
    private utils: Utils
  ) {
    this.item_qty = 1;
    this.sliderTwo =
    {
      isBeginningSlide: true,
      isEndSlide: false,
    };
    this.getDashboardData();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  ngOnInit() {
    this.menu.enable(true);

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  sort(chartData: ChartItem): ChartItem {
    let dados = chartData.getDados();
    let labels = chartData.getLabels();
    let list = [];
    for (let i = 0; i < dados.length; i++) {
      list.push({ dado: dados[i], label: labels[i] });
    }
    list.sort((a, b) => a.dado == b.dado ? 0 : (a.dado > b.dado ? -1 : 1))
    for (let i = 0; i < dados.length; i++) {
      dados[i] = list[i].dado;
      labels[i] = list[i].label;
    }
    chartData.setDados(dados);
    chartData.setLabels(labels);
    return chartData
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getDashboardData() {
    this.api.getDashboardData().then((dashboardData: Array<ChartItem>) => {
      let atividades = dashboardData.filter(n => n.getNome().toLowerCase().includes("atividades"))[0];
      let premios = dashboardData.filter(n => n.getNome().toLowerCase().includes("premios"))[0];
      let pilares = dashboardData.filter(n => n.getNome().toLowerCase().includes("pilar"))[0];
      let linhas = dashboardData.filter(n => n.getNome().toLowerCase().includes("linhas"))[0];
      const usuarios = dashboardData.filter(n => n.getNome().toLowerCase().includes("usuarios"))[0];

      atividades = this.sort(atividades);
      premios = this.sort(premios);
      pilares = this.sort(pilares);
      linhas = this.sort(linhas);

      let total = 0;
      for (let entry of usuarios.getDados()) {
        total += entry;
      }
      this.usuarios = new Array<any>();
      for (let i = 0; i < usuarios.getDados().length; i++) {
        this.usuarios.push({ label: usuarios.getLabels()[i], dado: usuarios.getDados()[i], porcentagem: (usuarios.getDados()[i] / total * 100).toPrecision(2) })
      }

      this.linhas = linhas;

      let colors = this.buildChartColors(premios);

      this.premiosResgatados = new Chart(this.barCanvas.nativeElement, {
        type: "bar",
        data: {
          labels: premios.getLabels(),
          datasets: [
            {
              data: premios.getDados(),
              backgroundColor: colors.background,
              borderColor: colors.border,
              borderWidth: 1
            }
          ]
        },
        options: {
          legend: {
            display: false
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true
                }
              }
            ]
          }
        }
      });

      colors = this.buildChartColors(atividades);
      this.atividadesPontuadas = new Chart(this.pieCanvas.nativeElement, {
        type: "pie",
        data: {
          labels: atividades.getLabels(),
          datasets: [
            {
              data: atividades.getDados(),
              backgroundColor: colors.background,
              borderColor: colors.border,
              borderWidth: 1
            }
          ]
        },
        options: {
          legend: {
            display: false
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true
                }
              }
            ]
          }
        }
      });

      colors = this.buildChartColors(pilares);
      this.pontosPilar = new Chart(this.lineBarCanvas.nativeElement, {
        type: "horizontalBar",
        data: {
          labels: pilares.getLabels(),
          datasets: [
            {
              data: pilares.getDados(),
              backgroundColor: colors.background,
              borderColor: colors.border,
              borderWidth: 1
            }
          ]
        },
        options: {
          legend: {
            display: false
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true
                }
              }
            ]
          }
        }
      });

      colors = this.buildChartColors(usuarios);
      this.pontosUsuarios = new Chart(this.lineCanvas.nativeElement, {
        type: "line",
        mode: 'vertical',
        data: {
          labels: usuarios.getLabels(),
          datasets: [
            {
              data: usuarios.getDados(),
              fill: false,
              steppedLine: false,
              backgroundColor: colors.background,
              borderColor: colors.border,
            }
          ]
        },
        options: {
          legend: {
            display: false
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true
                }
              }
            ]
          }
        }
      });

    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  buildChartColors(chartItem: ChartItem) {
    const backgroundColorPallete = [
      "rgba(79,33,112, 0.8)",
      "rgba(225,135,025, 0.8)",
      "rgba(40,120,25, 0.8)",
      "rgba(45,110,170, 0.8)",
      "rgba(165,35,35, 0.8)",
      "rgba(230,175,35, 0.8)",
      "rgba(153, 153, 153, 0.8)"
    ];
    const borderColorPallete = [
      "rgba(79,33,112, 1)",
      "rgba(225,135,025, 1)",
      "rgba(40,120,25, 1)",
      "rgba(45,110,170, 1)",
      "rgba(165,35,35, 1)",
      "rgba(230,175,35, 1)",
      "rgba(153, 153, 153, 1)"
    ];


    let index = 0;
    let backgroundColor = new Array<string>();
    let borderColor = new Array<string>();
    for (let i = 0; i < chartItem.getDados().length; i++) {
      if (index >= backgroundColorPallete.length) {
        index = 0;
      }
      if (i == chartItem.getDados().length - 1 && backgroundColor[0] == backgroundColorPallete[index]) {
        index++;
      }
      backgroundColor.push(backgroundColorPallete[index]);
      borderColor.push(borderColorPallete[index]);
      index++;
    }
    return { background: backgroundColor, border: borderColor };
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  logout() {
    this.authService.logout();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}