import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';
import { Factory } from './Factory';

export class Section {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Section>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    private id: number;
    private fabricaId: number;
    private nome: string;

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $secaoId
     * @return {number}
     */
    public getFabricaId(): number {
        return this.fabricaId;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        const logHeader = this.logHeader + "setId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + value);
        }
        this.id = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $fabricaId
     * @param {number} value
     */
    public setFabricaId(value: number) {
        const logHeader = this.logHeader + "setFabricaId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + value);
        }
        this.fabricaId = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        const logHeader = this.logHeader + "setNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + value);
        }
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public constructor(fabricaId: number, nome: string, id: number = undefined) {
        const logHeader = this.logHeader + "castFromJSON>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(fabricaId) || fabricaId <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>FABRICA>>" + fabricaId);
        }
        if (Utils.isStringInvalid(nome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + "NOME>>" + nome);
        }
        this.id = id;
        this.fabricaId = fabricaId;
        this.nome = nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Section {
        const logHeader = "Section>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let id = json.id;
        let fabricaId = json.fabricaId;
        let nome = json.nome;
        return new Section(fabricaId, nome, id);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}