import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { BehaviorSubject, Observable } from 'rxjs';
import { User } from 'src/models/User';
import { UserType } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

@Injectable({
  providedIn: 'root'
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class AuthenticationService {

  //-------------------------------------------------------------------------------------------------------------------------------//

  authenticationState = new BehaviorSubject(null);
  loggedUser: User;
  token = null;
  admin = false;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(private storage: Storage) {
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  setAuthObject(user: User) {
    this.storage.set(`user`, JSON.stringify(user));
    this.loggedUser = user;
    this.token = user.getToken();
    this.admin = user.getTipo() == UserType.ADMINISTRATOR
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getToken(): Observable<any> {
    return new Observable(obs => {
      if (this.token != null)
        return obs.next(this.token);

      this.storage.get('user').then(u => {
        const user = User.castFromJSON(u);
        this.loggedUser = user;
        this.admin = user.getTipo() == UserType.ADMINISTRATOR
        return obs.next(user.getToken());
      }).catch(() => {
        return obs.next(null);
      });
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isAdmin(): Observable<boolean> {
    return new Observable(obs => {
      this.storage.get('user').then(u => {
        const user = User.castFromJSON(u);
        this.loggedUser = user;
        this.admin = user.getTipo() == UserType.ADMINISTRATOR
        return obs.next(user.getTipo() == UserType.ADMINISTRATOR);
      }).catch(() => {
        return obs.next(false);
      });
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isFinalizado(): Observable<Boolean> {
    return new Observable(obs => {
      this.storage.get('user').then(u => {
        const user = User.castFromJSON(u);
        this.loggedUser = user;
        this.admin = user.getTipo() == UserType.ADMINISTRATOR
        return obs.next(user.isFinalizado());
      }).catch(() => {
        return obs.next(false);
      });
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//


  logout() {
    this.token = null;
    this.loggedUser = null;
    this.storage.clear();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isAuthenticated(): Observable<boolean> {
    return new Observable(obs => {
      this.storage.get('user').then(u => {
        const user = User.castFromJSON(u);
        this.loggedUser = user;
        return obs.next(user.getToken() != null);
      }).catch(() => {
        return obs.next(false);
      });
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}