import { UserType, ErrorMessage, Constants } from 'src/commons/Constants';
import { Utils } from 'src/commons/Utils';
import { stringify } from '@angular/core/src/util';
import { Line } from './Line';
import { Factory } from './Factory';

//-------------------------------------------------------------------------------------------------------------------------------//

export class User {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader: string = "User>>"

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Fields */

    private admin: Boolean;
    private ativo: Boolean;
    private email: string;
    private finalizado: Boolean;
    private id: number;
    private linhaId: number;
    private linha: Line;
    private matricula: string;
    private nome: string;
    private senha: string;
    private token: string;
    private saldo: number;
    private foto: string;
    public checked: Boolean;
    private linhaNome: string;
    private secaoNome: string;
    private fabricaNome: string;
    private equipeId: number;
    private equipeNome: string;
    private telefoneCelular: string;
    private grauEscolaridade: string;

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Getters */

    /**
     * Getter $ativo
     * @return {Boolean}
     */
    public isAtivo(): Boolean {
        return this.ativo;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $admin
     * @return {Boolean}
     */
    public isAdmin(): Boolean {
        return this.admin;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $finalizado
     * @return {Boolean}
     */
    public isFinalizado(): Boolean {
        return this.finalizado;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $token
     * @return {string}
     */
    public getToken(): string {
        return this.token;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $tipo
     * @return {UserType}
     */
    public getTipo(): UserType {
        if (this.admin) {
            return UserType.ADMINISTRATOR;
        }
        else {
            return UserType.GENERIC;
        }
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $senha
     * @return {string}
     */
    public getSenha(): string {
        return this.senha;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $linhaNome
     * @return {string}
     */
    public getLinhaNome(): string {
        return this.linhaNome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $secaoNome
     * @return {string}
     */
    public getSecaoNome(): string {
        return this.secaoNome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $fabricaNome
     * @return {string}
     */
    public getFabricaNome(): string {
        return this.fabricaNome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $matricula
     * @return {string}
     */
    public getMatricula(): string {
        return this.matricula;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $linhaId
     * @return {number}
     */
    public getLinhaId(): number {
        return this.linhaId;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $email
     * @return {string}
     */
    public getEmail(): string {
        return this.email;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $saldo
     * @return {number}
     */
    public getSaldo(): number {
        return this.saldo;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $foto
     * @return {string}
     */
    public getFoto(): string {
        return this.foto;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $equipeId
     * @return {number}
     */
    public getEquipeId(): number {
        return this.equipeId;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $equipeNome
     * @return {string}
     */
    public getEquipeNome(): string {
        return this.equipeNome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $telefone
     * @return {string}
     */
    public getTelefone(): string {
        return this.telefoneCelular;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $escolaridade
     * @return {string}
     */
    public getEscolaridade(): string {
        return this.grauEscolaridade;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Setters */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $admin
     * @param {Boolean} value
     */
    public setAdmin(value: Boolean) {
        const logHeader = this.logHeader + "setAdmin>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + value)
        }
        this.admin = value;
    }
    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $ativo
     * @param {Boolean} value
     */
    public setAtivo(value: Boolean) {
        const logHeader = this.logHeader + "setAtivo>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + value)
        }
        this.ativo = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {String} value
     */
    public setNome(value: string) {
        const logHeader = this.logHeader + "setNome>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + value)
        }
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $linhaId
     * @param {number} value
     */
    public setLinhaId(value: number) {
        const logHeader = this.logHeader + "setLinhaId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "LINHA_ID>>" + value);
        }
        this.linhaId = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $email
     * @param {string} value
     */
    public setEmail(value: string) {
        const logHeader = this.logHeader + "setEmail>>";
        if (Utils.isEmailInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_EMAIL + "EMAIL>>" + JSON.stringify(value));
        }
        this.email = value.toLowerCase();
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $matricula
     * @param {string} value
     */
    public setMatricula(value: string) {
        const logHeader = this.logHeader + "setMatricula>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + "MATRICULA>>" + JSON.stringify(value));
        }
        this.matricula = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $saldo
     * @param {number} value
     */
    public setSaldo(value: number) {
        const logHeader = this.logHeader + "setSaldo>>";
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "SALDO>>" + JSON.stringify(value));
        }
        this.saldo = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $saldo
     * @param {number} value
     */
    public addSaldo(value: number) {
        const logHeader = this.logHeader + "addSaldo>>";
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "SALDO>>" + JSON.stringify(value));
        }
        this.saldo += value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $saldo
     * @param {number} value
     */
    public removeSaldo(value: number) {
        const logHeader = this.logHeader + "removeSaldo>>";
        if (Utils.isVarInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "SALDO>>" + JSON.stringify(value));
        }
        this.saldo -= value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $foto
     * @param {string} value
     */
    public setFoto(value: string) {
        const logHeader = this.logHeader + "setFoto>>";
        if (Utils.isStringInvalid(value)) {
            value = Constants.DEFAULT_USER_PHOTO;
        }
        this.foto = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $equipeId
     * @param {number} value
     */
    public setEquipeId(value: number) {
        const logHeader = this.logHeader + "setEquipeId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + JSON.stringify(value));
        }
        this.equipeId = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $equipeNome
     * @param {string} value
     */
    public setEquipeNome(value: string) {
        const logHeader = this.logHeader + "setEquipeNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + JSON.stringify(value));
        }
        this.equipeNome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $telefone
     * @param {string} value
     */
    public setTelefone(value: string) {
        const logHeader = this.logHeader + "setTelefone>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + JSON.stringify(value));
        }
        this.telefoneCelular = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $senha
     * @param {string} value
     */
    public setSenha(value: string) {
        const logHeader = this.logHeader + "setSenha>>";
        this.senha = value;
    }
    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $escolaridade
     * @param {string} value
     */
    public setEscolaridade(value: string) {
        const logHeader = this.logHeader + "setEscolaridade>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + JSON.stringify(value));
        }
        this.grauEscolaridade = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(
        admin: Boolean = undefined,
        ativo: Boolean = undefined,
        email: string = undefined,
        finalizado: Boolean = undefined,
        id: number = undefined,
        linhaId: number = undefined,
        matricula: string = undefined,
        nome: string = undefined,
        senha: string = undefined,
        token: string = undefined,
        saldo: number = undefined,
        foto: string = undefined,
        linhaNome: string = undefined,
        secaoNome: string = undefined,
        fabricaNome: string = undefined,
        equipeId: number = undefined,
        equipeNome: string = undefined,
        telefone: string = undefined,
        escolaridade: string = undefined,
    ) {
        const logHeader = this.logHeader + "constructor>>";
        if (Utils.isVarInvalid(admin)) {
            this.admin = false;
        }
        else {
            this.admin = admin;
        }

        if (Utils.isVarInvalid(ativo)) {
            this.ativo = false;
        }
        else {
            this.ativo = ativo;
        }

        // if (Utils.isStringInvalid(email)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_STRING + "EMAIL>>" + email);
        // }

        if (!Utils.isStringInvalid(email)) {
            email = email.toLowerCase();
        }

        if (Utils.isVarInvalid(finalizado)) {
            this.finalizado = false;
        }
        else {
            this.finalizado = finalizado;
        }

        // if (Utils.isVarInvalid(id)) {
        //     throw new Error(logHeader + ErrorType.INVALID_STRING + "ID>>" + id);
        // }

        // if (Utils.isVarInvalid(linhaId)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "LINHA_ID>>" + linhaId);
        // }

        // if (Utils.isStringInvalid(matricula)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_STRING + "MATRICULA>>" + matricula);
        // }

        // if (Utils.isStringInvalid(nome)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_STRING + "NOME>>" + nome);
        // }

        // if (Utils.isStringInvalid(senha)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_STRING + "SENHA>>" + senha);
        // }

        // if (Utils.isStringInvalid(token)) {
        //     throw new Error(logHeader + ErrorType.INVALID_STRING + "TOKEN>>" + token);
        // }

        // if (Utils.isVarInvalid(saldo)) {
        //     throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "SALDO>>" + saldo);
        // }
        if (Utils.isStringInvalid(foto)) {
            foto = Constants.DEFAULT_USER_PHOTO;
        }

        this.email = email;
        this.id = id;
        this.linhaId = linhaId;
        this.matricula = matricula;
        this.nome = nome;
        this.senha = senha;
        this.token = token;
        this.saldo = saldo;
        this.foto = "https://mdlzplusstorage.blob.core.windows.net/user/" + id;
        this.linhaNome = linhaNome;
        this.secaoNome = secaoNome;
        this.fabricaNome = fabricaNome;
        this.equipeId = equipeId;
        this.equipeNome = equipeNome;
        this.telefoneCelular = telefone;
        this.grauEscolaridade = escolaridade;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): User {
        const logHeader = "User>>castFromJSON>>";
        Utils.log(logHeader);
        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        return new User(
            json.admin,
            json.ativo,
            json.email,
            json.finalizado,
            json.id,
            json.linhaId,
            json.matricula,
            json.nome,
            json.senha,
            json.token,
            json.saldo,
            json.foto,
            json.linhaNome,
            json.secaoNome,
            json.fabricaNome,
            json.equipeId,
            json.equipeNome,
            json.telefoneCelular,
            json.grauEscolaridade
        );
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}