import { Component, OnInit } from '@angular/core';
import { ZeramentoPage } from '../users/zeramento/zeramento.page';
import { ModalController, PopoverController } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pontos-menu',
  templateUrl: './pontos-menu.page.html',
  styleUrls: ['./pontos-menu.page.scss'],
})
export class PontosMenuPage implements OnInit {

  constructor(private router: Router,  private popoverController: PopoverController) { }

  ngOnInit() {
  }
  async newPontuacao() {
    this.popoverController.dismiss();
    this.router.navigateByUrl('/zeramento');
  }
}
