import { Reward } from './Reward';
import { ErrorMessage } from 'src/commons/Constants';
import { Utils } from 'src/commons/Utils';

export class CartItem {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "CartItem>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Fields */

    private premio: Reward;
    private id: number;
    private quantity: number;

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Getters */

    /**
     * Getter $premio
     * @return {Reward}
     */
    public getPremio(): Reward {
        return this.premio;
    }

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    /**
     * Getter $quantidade
     * @return {number}
     */
    public getQuantidade(): number {
        return this.quantity;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Setters */

    /**
     * Setter $premio
     * @param {Reward} value
     */
    public setPremio(value: Reward) {
        this.premio = value;
        this.id = this.premio.getId();
    }

    /**
     * Setter $quantidade
     * @param {number} value
     */
    public setQuantidade(value: number) {
        const logHeader = this.logHeader + "setQuantidade"
        if (value < 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + value);
        }
        this.quantity = value;
    }

    /**
     * Setter $quantidade
     * @param {number} value
     */
    public addQuantidade(value: number) {
        const logHeader = this.logHeader + "addQuantidade"
        this.setQuantidade(Utils.isVarInvalid(this.getQuantidade()) ? 0 : this.getQuantidade() + value);
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(premio: Reward = undefined, quantidade: number = 1) {
        this.premio = premio;
        if (!Utils.isVarInvalid(premio)) {
            this.id = premio.getId();
        }
        this.quantity = quantidade;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): CartItem {
        const logHeader = "CartItem>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let premio = Reward.castFromJSON(json.premio);
        let quantidade = json.quantidade;
        return new CartItem(premio, quantidade);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}