import { Utils } from 'src/commons/Utils';
import { ErrorMessage, Constants } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Reward {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Reward>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Fields */

    private id: number;
    private valor: number;
    private nome: string;
    private foto: string;
    private descricao: string;
    private quantidade: number;
    private ativo: boolean;

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Getters */

    /**
     * Getter $id
     * @return {string}
     */
    public getId(): number {
        return this.id;
    }

    /**
     * Getter $cost
     * @return {number}
     */
    public getValor(): number {
        return this.valor;
    }

    /**
     * Getter $name
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    /**
     * Getter $image
     * @return {string}
     */
    public getFoto(): string {
        return this.foto;
    }

    /**
     * Getter $description
     * @return {string}
     */
    public getDescricao(): string {
        return this.descricao;
    }

    /**
     * Getter $quantidade
     * @return {number}
     */
    public getQuantidade(): number {
        return this.quantidade;
    }

    /**
     * Getter $ativo
     * @return {boolean}
     */
    public isAtivo(): boolean {
        return this.ativo;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Setters */


    /**
     * Setter $ativo
     * @param {boolean} value
     */
    public setAtivo(value: boolean) {
        this.ativo = value;
    }

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        this.id = value;
    }

    /**
     * Setter $valor
     * @param {number} value
     */
    public setValor(value: number) {
        this.valor = value;
    }

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        this.nome = value;
    }

    /**
     * Setter $foto
     * @param {string} value
     */
    public setFoto(value: string) {
        this.foto = value;
    }

    /**
     * Setter $descricao
     * @param {string} value
     */
    public setDescricao(value: string) {
        this.descricao = value;
    }

    /**
     * Setter $quantidade
     * @param {number} value
     */
    public setQuantidade(value: number) {
        this.quantidade = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    public constructor(
        id: number = undefined,
        valor: number = undefined,
        nome: string = undefined,
        descricao: string = undefined,
        foto: string = undefined,
        quantidade: number = undefined,
        ativo: boolean = undefined) {
        if (Utils.isVarInvalid(foto)) {
            foto = Constants.DEFAULT_REWARD_PHOTO;
        }
        this.id = id;
        this.valor = valor;
        this.nome = nome;
        this.descricao = descricao;
        this.foto = "https://mdlzplusstorage.blob.core.windows.net/premio/" + id;
        this.quantidade = quantidade;
        this.ativo = ativo;

    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Reward {
        const logHeader = "Reward>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let id = json.id;
        let nome = json.nome;
        let valor = json.valor;
        let descricao = json.descricao;
        let foto = json.foto;
        let quantidade = json.quantidade;
        let ativo = json.ativo
        return new Reward(id, valor, nome, descricao, foto, quantidade, ativo);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}