import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalController, PopoverController, NavParams } from '@ionic/angular';
import { Activity } from 'src/models/Activity';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { ToastText, ErrorMessage } from 'src/commons/Constants';
import { PageDataService } from 'src/providers/page-data/page-data.service';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-pontos-modal',
  templateUrl: './pontos-modal.page.html',
  styleUrls: ['./pontos-modal.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class PontosModalPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "PontosModalPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public activities: Array<Activity> = new Array<Activity>();
  public selectedActivity: Activity;
  private users: Array<number> = new Array<number>();
  private remove: boolean = false;
  private page: number = 1;
  private totalPages: number;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    public popoverController: PopoverController,
    private navParams: NavParams,
    private api: BackendApiProvider,
    private utils: Utils,
    private pageData: PageDataService
  ) {
    this.users = this.navParams.get('users');
    this.remove = this.navParams.get('remove');
    this.getActivities();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getActivities() {
    if (this.remove) {
      this.api.getActivities(null).then((response: any) => {
        let activities = response.list;
        this.activities = activities.filter((n) => n.getValor() < 0);
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }
    else {
      this.api.getActivities(null).then((response: any) => {
        let activities = response.list;
        this.activities = activities.filter((n) => n.getValor() > 0);
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async addPoints() {
    const logHeader = this.logHeader + "addPoints>>";
    if (Utils.isVarInvalid(this.selectedActivity)) {
      this.utils.simpleToastError(ToastText.INSERT_REASON)
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "ACTIVITY>>" + JSON.stringify(this.selectedActivity));
    }
    this.api.addPoints(this.users, this.selectedActivity).then(() => {
      this.utils.simpleToast(ToastText.POINTS_ADDED);
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
    }).finally(() => {
      this.pageData.listUserEvent.emit({ users: this.users, value: this.selectedActivity.getValor() });
      this.popoverController.dismiss();
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  activitySelected(activity: Activity) {
    this.selectedActivity = activity;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
