import { Component, OnInit } from '@angular/core';
import { PopoverController, NavParams } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Activity } from 'src/models/Activity';
import { Utils } from 'src/commons/Utils';
import { ErrorMessage, ToastText } from 'src/commons/Constants';
import { Schooling } from 'src/models/Schooling';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-atividades-edit',
  templateUrl: './atividades-edit.page.html',
  styleUrls: ['./atividades-edit.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class AtividadesEditPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "AtividadesEditPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public pilares: Array<Schooling> = new Array<Schooling>();
  public pilar: Schooling;
  private activity: Activity;
  public name: string;
  public value: number;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public popoverController: PopoverController, private navParams: NavParams, private api: BackendApiProvider, private utils: Utils) {
    const logHeader = this.logHeader + "constructor>>";
    this.activity = this.navParams.get('activity');
    if (!Utils.isVarInvalid(this.activity)) {
      this.activity = Activity.castFromJSON(this.activity)
      this.name = this.activity.getNome();
      this.value = this.activity.getValor();
      switch (this.activity.getPilar()) {
        case "Liderana":
          {
            this.pilar = new Schooling(1, this.activity.getPilar());
            break;
          }
        case "SSMA":
          {
            this.pilar = new Schooling(2, this.activity.getPilar());
            break;
          }
        case "MF":
          {
            this.pilar = new Schooling(3, this.activity.getPilar());
            break;
          }
        case "IIM":
          {
            this.pilar = new Schooling(4, this.activity.getPilar());
            break;
          }
        case "MP":
          {
            this.pilar = new Schooling(5, this.activity.getPilar());
            break;
          }
        case "MA":
          {
            this.pilar = new Schooling(6, this.activity.getPilar());
            break;
          }
        case "QM":
          {
            this.pilar = new Schooling(7, this.activity.getPilar());
            break;
          }
        case "WPI":
          {
            this.pilar = new Schooling(8, this.activity.getPilar());
            break;
          }
        default: {
          this.pilar = new Schooling(1, "Liderana");
          break;
        }
      }
    }
    this.pilares.push(new Schooling(1, "Liderana"));
    this.pilares.push(new Schooling(2, "SSMA"));
    this.pilares.push(new Schooling(3, "MF"));
    this.pilares.push(new Schooling(4, "IIM"));
    this.pilares.push(new Schooling(5, "MP"));
    this.pilares.push(new Schooling(6, "MA"));
    this.pilares.push(new Schooling(7, "QM"));
    this.pilares.push(new Schooling(8, "WPI"));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  compareById(o1, o2) {
    if (o1 == null || o2 == null) {
      return false;
    }
    return o1.id === o2.id;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async edit() {
    const logHeader = this.logHeader + "edit>>";
    if (Utils.isStringInvalid(this.name)) {
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + this.name)
    }
    if (Utils.isVarInvalid(this.pilar)) {
      this.utils.simpleToast(ToastText.INSERT_NAME);
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>PILAR>>" + this.pilar);
    }
    if (Utils.isVarInvalid(this.value)) {
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + this.value)
    }
    if (Utils.isVarInvalid(this.activity)) {
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>" + this.activity)
    }
    this.activity.setNome(this.name);
    this.activity.setValor(this.value);
    this.activity.setPilar(this.pilar.description);
    this.api.editActivity(this.activity).then(() => {
      this.utils.simpleToast(ToastText.ACTIVITY_EDITED);
      this.popoverController.dismiss(this.activity);
    }).catch((error) => {
      this.utils.simpleToastError(ToastText.SOMETHING_WENT_WRONG);
      this.DismissClick();
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
