import { Component, OnInit } from '@angular/core';
import { Factory } from 'src/models/Factory';
import { Section } from 'src/models/Section';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { PopoverController, ModalController, NavParams } from '@ionic/angular';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Line } from 'src/models/Line';
import { ToastText, ErrorMessage, ErrorType } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-new',
  templateUrl: './new.page.html',
  styleUrls: ['./new.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class NewPageL {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "NewPageL>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public modoEdicao: boolean;
  private line: Line;
  public name: string;
  public factory: Factory;
  public factoryId: number;
  public factories: Array<Factory> = new Array<Factory>();
  public sectionId: number;
  public sections: Array<Section> = new Array<Section>();
  public section: Section;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private modalCtrl: ModalController,
    private api: BackendApiProvider,
    private utils: Utils,
    private popoverController: PopoverController,
    private pageDataService: PageDataService,
    private navParams: NavParams
  ) {
    this.line = this.navParams.get('line');
    this.modoEdicao = !Utils.isVarInvalid(this.line);
    if (this.modoEdicao) {
      this.name = this.line.getNome();
      this.sectionId = this.line.getSecaoId();
      this.api.getSections().then((sections: Array<Section>) => {
        let section = sections.filter((n) => n.getId() == this.sectionId)[0];
        this.factoryId = section.getFabricaId();
        this.getSections();
      })
    }
    else {
      this.getSections();

    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 


  async closeModal() {
    await this.modalCtrl.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  compareById(o1, o2) {
    if (o1 == null || o2 == null) {
      return false;
    }
    return o1.id === o2.id;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getSections() {
    this.api.getSections(this.factoryId).then((sections: Array<Section>) => {
      this.sections = sections;
      if (this.modoEdicao) {
        this.section = this.sections.filter(n => n.getId() == this.sectionId)[0];
      }
      this.api.getFactories().then((factories: Array<Factory>) => {
        this.factories = factories;
        if (this.modoEdicao) {
          this.factory = this.factories.filter(n => n.getId() == this.section.getFabricaId())[0];
        }
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
      });
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  factorySelected(factory: Factory) {
    this.factoryId = factory.getId();
    this.api.getSections(factory.getId()).then((sections: Array<Section>) => {
      this.sections = sections;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  sectionSelected(section: Section) {
    this.sectionId = section.getId();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  addNew() {
    const logHeader = this.logHeader + "addNew>>";
    if (Utils.isVarInvalid(this.factoryId)) {
      this.utils.simpleToast(ToastText.INSERT_FACTORY);
      throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>FACTORY>>" + this.factoryId);
    }
    if (Utils.isVarInvalid(this.sectionId)) {
      this.utils.simpleToast(ToastText.INSERT_SECTION);
      throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>SECTION>>" + this.sectionId);
    }
    if (Utils.isStringInvalid(this.name)) {
      this.utils.simpleToastError(ToastText.INSERT_NAME);
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>NAME>>" + this.name);
    }
    if (!this.modoEdicao) {
      this.api.createLine(new Line(this.sectionId, this.name)).then((response: Section) => {
        this.pageDataService.setData('newLine', response);
        this.pageDataService.pushUsers(response);
        this.popoverController.dismiss();
        this.closeModal();
        this.utils.simpleToast(ToastText.CREATION_SUCCESSFUL);
      }).catch((error) => {
        let message = JSON.parse(error._body).message as string;
        if (message.includes(ErrorType.DUPLICATED)) {
          this.utils.simpleToastError(ToastText.ALREADY_EXISTS);
        }
        else {
          this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
        }
      });
    }
    else {
      this.line.setNome(this.name);
      this.line.setSecaoId(this.sectionId);
      this.api.editLine(this.line).then((response: Line) => {
        this.pageDataService.setData('newLine', response);
        this.pageDataService.pushUsers(response);
        this.popoverController.dismiss();
        this.closeModal();
        this.utils.simpleToast(ToastText.EDIT_SUCCESSFUL);
      }).catch((error) => {
        let message = JSON.parse(error._body).message as string;
        if (message.includes(ErrorType.DUPLICATED)) {
          this.utils.simpleToastError(ToastText.ALREADY_EXISTS);
        }
        else {
          this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
        }
      });
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
