import { Injectable } from '@angular/core';
import { Utils } from 'src/commons/Utils';

//-------------------------------------------------------------------------------------------------------------------------------//

@Injectable({
  providedIn: 'root'
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class LoaderService {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private loader;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(private utils: Utils) {
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  public present(message: string = undefined, duration: number = undefined) {
    this.utils.simpleLoading(message, duration).then((loader) => {
      this.loader = loader;
      this.loader.present();
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  public dismiss() {
    this.loader.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
