import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Factory {
    private readonly logHeader = "Factory>>";
    private id: number;
    private nome: string;

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        const logHeader = this.logHeader + "setId>>";
        if (Utils.isVarInvalid(value) || value <= 0) {
            throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + value);
        }
        this.id = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        const logHeader = this.logHeader + "setNome>>";
        if (Utils.isStringInvalid(value)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>" + value);
        }
        this.nome = value;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public constructor(nome: string, id: number = undefined) {
        const logHeader = this.logHeader + "constructor>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(nome)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "NOME>>" + nome);
        }
        this.id = id;
        this.nome = nome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Factory {
        const logHeader = "User>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        if (Utils.isVarInvalid(json)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "JSON>>" + JSON.stringify(json));
        }
        let id = json.id;
        let nome = json.nome;
        return new Factory(nome, id);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}