import { Component, OnInit } from '@angular/core';
import { ModalController, ActionSheetController, NavParams } from '@ionic/angular';
import { User } from 'src/models/User';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { Router } from '@angular/router';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Team } from 'src/models/Team';
import { Observable } from 'rxjs';
import { ToastText } from 'src/commons/Constants';

@Component({
  selector: 'app-users-modal',
  templateUrl: './users-modal.page.html',
  styleUrls: ['./users-modal.page.scss'],
})
export class UsersModalPage {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "UsersModalPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public multicheck: boolean = false;
  private users: Array<User>;
  public userList: Array<User>;
  private searchTerm: string;
  private selectedUsers: Array<User> = new Array<User>();
  private team: Team
  private page: number = 1;
  private totalPages: number;
  public imageConcatString = Utils.generateRandomString();
  private choosingLeader: boolean;

  //-------------------------------------------------------------------------------------------------------------------------------// 

  constructor(public modalController: ModalController,
    private authService: AuthenticationService,
    private router: Router,
    private api: BackendApiProvider,
    private utils: Utils,
    private pageDataService: PageDataService,
    private navParams: NavParams) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.team = this.navParams.get('team');
    this.choosingLeader = !Utils.isVarInvalid(this.navParams.get('leader'));
    if (Utils.isVarInvalid(this.team)) {
      this.team = new Team("mock", [], undefined, undefined)
    }
    else if (this.choosingLeader && !Utils.isVarInvalid(this.team.getLiderId())) {
      this.api.getUserById(this.team.getLiderId()).then((leader: User) => {
        this.selectedUsers.push(leader);
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }
    else {
      this.selectedUsers = this.team.getMembros();
    }
    for (let user of this.selectedUsers) {
      user.checked = true;
    }
    this.getUsers();
  }

  //-------------------------------------------------------------------------------------------------------------------------------// 

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getUsers() {
    const logHeader = this.logHeader + "getUsers>>";
    Utils.log(logHeader);
    this.api.getUsers(1).then((response: any) => {
      this.imageConcatString = Utils.generateRandomString();
      let users: Array<User> = response.users;
      this.totalPages = response.totalPages
      this.users = users;
      this.filter(this.searchTerm);
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));

  }
  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.userList = this.users;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.userList = this.users;
      return;
    }
    this.userList = this.users.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()) || n.getEmail().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  userChange(user: User, checked: boolean, evt: any = undefined) {

    if (checked) {
      if (this.choosingLeader) {
        this.selectedUsers = new Array<User>();
        this.selectedUsers.push(user);
      }
      else if (this.selectedUsers.filter(n => n.getId() == user.getId()).length == 0) {
        this.selectedUsers.push(user);
      }
    }
    else {
      this.selectedUsers.splice(this.selectedUsers.indexOf(this.selectedUsers.filter(n => n.getId() == user.getId())[0]), 1);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isUserInTeam(id: number) {
    if (Utils.isVarInvalid(this.team)) {
      return false;
    }
    else {
      return this.team.getLiderId() == id || this.team.getMembros().filter(n => n.getId() == id).length > 0
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  save() {
    this.pageDataService.setData('selectedUsers', this.selectedUsers);
    this.pageDataService.pushUsers(this.selectedUsers);
    this.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    this.page++;
    const infiniteScroll = event.target;
    this.api.getUsers(this.page).then((response: any) => {
      this.imageConcatString = Utils.generateRandomString();
      let users: Array<User> = response.users;
      this.totalPages = response.totalPages;
      this.users = this.users.concat(users);
      this.filter(this.searchTerm);
      infiniteScroll.complete();
      infiniteScroll.disabled = this.page >= this.totalPages;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
