import { Injectable } from '@angular/core';
import { Constants, ErrorMessage, ToastText, ErrorType, MenuText } from './Constants';
import { LoadingOptions, AlertOptions, ToastOptions } from '@ionic/core';
import { Camera, CameraOptions, PictureSourceType, DestinationType } from '@ionic-native/camera/ngx';
import { LoadingController, AlertController, ToastController, ModalController, Platform } from '@ionic/angular';
import { User } from 'src/models/User';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { File } from '@ionic-native/file/ngx';
import { Crop } from '@ionic-native/crop/ngx';

@Injectable()
export class Utils {

    private readonly logHeader = "Utils>>"

    //-------------------------------------------------------------------------------------------------------------------------------//

    constructor(
        private camera: Camera,
        private loadingController: LoadingController,
        private alertController: AlertController,
        private toastController: ToastController,
        private file: File,
        private crop: Crop,
        private platform: Platform
    ) {
        const logHeader = this.logHeader + "constructor>>";
        Utils.log(logHeader);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public loading(options: LoadingOptions) {
        return this.loadingController.create(options);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public simpleLoading(message: string = "", duration: number = 3000) {
        const logHeader = this.logHeader + "simpleLoading>>"
        Utils.log(logHeader);
        return this.loadingController.create({
            spinner: 'bubbles',
            cssClass: 'mdlz-loader-class',
            message: message,
            showBackdrop: true,
            duration: duration,
        });
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public alert(options: AlertOptions) {
        return this.alertController.create(options);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public simpleAlert(title: string, message: string) {
        this.alertController.create({
            header: title,
            message: message,
            buttons: [MenuText.OK]
        }).then((alert) => {
            alert.present();
        })
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public toast(options: ToastOptions) {
        return this.toastController.create(options)
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public simpleToast(message: string, duration: number = 3000) {
        const logHeader = this.logHeader + "simpleToast>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(message)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + "MESSAGE>>" + JSON.stringify(message));
        }
        return this.toastController.create({
            message: message,
            duration: duration,
            animated: true,
            cssClass: "toast-mdlz"

        }).then((toast) => {
            toast.present();
        })
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public simpleToastError(message: string, duration: number = 3000) {
        const logHeader = this.logHeader + "simpleToast>>";
        Utils.log(logHeader);
        if (Utils.isStringInvalid(message)) {
            throw new Error(logHeader + ErrorMessage.INVALID_STRING + "MESSAGE>>" + JSON.stringify(message));
        }
        return this.toastController.create({
            message: message,
            duration: duration,
            animated: true,
            cssClass: "toast-negativo"

        }).then((toast) => {
            toast.present();
        })
    }
    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Log message if application is in TEST mode
     * 
     * @param message 
     */
    static log(message: any) {
        if (Constants.TEST) {
            console.log(new Date().toISOString() + ">>" + message);
        }
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if string is valid or not.
     * 
     * @param text string - String to be verified.
     * @returns {boolean} boolean - True if input string is invalid, False otherwise.
     */
    static isStringInvalid(text: string): boolean {
        const logHeader = "Utils>>isStringInvalid>>";
        // this.log(logHeader);
        return this.isVarInvalid(text) || text.length === 0;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if array is valid or not.
     * 
     * @param {any[]} array any[] - Array to be verified.
     * @returns {boolean} boolean - True if input string is invalid, False otherwise.
     */
    static isArrayInvalid(array: any[]): boolean {
        const logHeader = "Utils>>isArrayInvalid>>";
        // this.log(logHeader);
        return this.isVarInvalid(array) || array.length === 0;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if variable is null or undefined.
     * 
     * @param variable any - Variable to be verified.
     * @returns {boolean} boolean - True if input variable is null or undefined, False otherwise.
     */
    static isVarInvalid(variable: any): boolean {
        const logHeader = "Utils>>isVarInvalid>>";
        // this.log(logHeader);
        return variable === null || variable === undefined;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if a given date is null, undefined or in the past.
     * 
     * @param date date - Date to be verified
     * @returns {boolean} boolean - True if input date is invalid or in the past, False otherwise.
     */
    static isDatePastOrInvalid(date: Date): boolean {
        return this.isVarInvalid(date) || date.getTime() <= (new Date()).getTime();
    }

    //-------------------------------------------------------------------------------------------------------------------------------//
    /**
     * Verifies if a given date is null, undefined or in the future.
     * 
     * @param date date - Date to be verified
     * @returns {boolean} boolean - True if input date is invalid or in the future, False otherwise.
     */
    static isDateFutureOrInvalid(date: Date): boolean {
        return this.isVarInvalid(date) || date.getTime() > (new Date()).getTime();
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if a given string is null, undefined or is not a valid e-mail address.
     * 
     * @param {string} email string - E-mail to be verified
     * @returns {boolean} boolean - True if input e-mail is invalid, False otherwise.
     */
    static isEmailInvalid(email: string): boolean {
        return this.isStringInvalid(email) || !(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email));
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Verifies if a given string is null, undefined or is not a valid phone number.
     * 
     * @param {string} phone string - Phone number to be verified
     * @returns {boolean} boolean - True if input phone number is invalid, False otherwise.
     */
    static isPhoneInvalid(phone: string): boolean {
        return this.isStringInvalid(phone) || /^(?:(?:\+|00)?(55)\s?)?(?:\(?([1-9][0-9])\)?\s?)?(?:((?:9\d|[2-9])\d{3})\-?(\d{4}))$/.test(phone);
    }

    /**
     * Returns True if given object has "status" field and "status" equals 200.
     * 
     * @param response Object to be tested
     */
    static hasOkStatus(response: any): boolean {
        return (!Utils.isVarInvalid(response)) && (!Utils.isVarInvalid(response.status)) && response.status == 200;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static generateUniqueId(prefix: string): string {
        return prefix + "_" + Date.now().toString() + "_" + Math.random().toString(36).substr(2, 9);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    /**
     * Converts Date to eye-friendly string
     * 
     * @param date Date object to be converted
     */
    static dateToString(date: Date): string {
        const logHeader = "Utils>>dateToString>>";
        if (Utils.isVarInvalid(date)) {
            throw new Error(logHeader + ErrorMessage.INVALID_DATE + "DATE>>" + date);
        }
        Utils.log(logHeader + "DATE>>" + JSON.stringify(date) + "STRING>>" + (date.getDay() < 10 ? '0' + date.getDay() : date.getDay()) + '/' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)) + ' ' + date.getHours() + ':' + (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()))
        return (date.getDay() < 10 ? '0' + date.getDay() : date.getDay()) + '/' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)) + ' ' + date.getHours() + ':' + (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static dismissLoading(loading: HTMLIonLoadingElement) {
        if (!Utils.isVarInvalid(loading)) {
            loading.dismiss();
        }
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static generateRandomString(): string {
        return Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public async takePicture(sourceType: PictureSourceType) {
        var options: CameraOptions = {
            quality: 100,
            sourceType: sourceType,
            destinationType: this.camera.DestinationType.FILE_URI,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            saveToPhotoAlbum: false,
            correctOrientation: true
        };
        return this.camera.getPicture(options);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public static getImageBlob(dataURI) {
        const byteString = window.atob(dataURI);
        const arrayBuffer = new ArrayBuffer(byteString.length);
        const int8Array = new Uint8Array(arrayBuffer);
        for (let i = 0; i < byteString.length; i++) {
            int8Array[i] = byteString.charCodeAt(i);
        }
        const blob = new Blob([int8Array], { type: 'image/jpeg' });
        return blob;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public saveImage(blob, name) {
        return new Promise((resolve, reject) => {
            this.file.checkDir(this.file.dataDirectory, 'pictures')
                .then(_ => {
                    this.file.writeFile(this.file.dataDirectory + 'pictures/', name, blob).then(response => {
                        resolve(response);
                    }).catch(err => {
                        reject(err);
                    })
                })
                .catch(err => {
                    this.file.createDir(this.file.dataDirectory, 'pictures', false).then(result => {
                        this.file.writeFile(this.file.dataDirectory + 'pictures/', name, blob).then(response => {
                            resolve(response);
                        }).catch(err => {
                            reject(err);
                        })
                    })
                });
        })
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public resolveLocalFileSystemURL(imageURI) {
        return this.file.resolveLocalFilesystemUrl(imageURI);
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    public cropPicture(imageURI) {
        return new Promise<any>((resolve, reject) => {
            if (!(this.platform.is('desktop') || this.platform.is('mobileweb'))) {
                this.crop.crop(imageURI).then((imageData) => {
                    let imageName;
                    if (imageData.indexOf('?') > 0) {
                        imageName = imageData.slice(imageData.lastIndexOf('/') + 1, imageData.indexOf('?'));
                    }
                    else {
                        imageName = imageData.slice(imageData.lastIndexOf('/') + 1, imageData.length);
                    }
                    let imagePath = imageData.slice(0, imageData.lastIndexOf('/'));
                    this.file.readAsDataURL(imagePath, imageName).then((base64) => {
                        resolve(base64);
                    }).catch((error) => reject(error));
                }).catch((error) => reject(error));
            }
            else {
                resolve("data:image/jpeg;base64," + imageURI)
            }
        });
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    handleErrorToast(error: Error);
    handleErrorToast(error: string);
    handleErrorToast(errorInput: any) {
        let error: string;
        if (typeof errorInput == "string") {
            error = errorInput;
        }
        else if (typeof errorInput) {
            error = errorInput.message;
        }
        if (error.includes(ErrorType.DUPLICATED)) {
            this.simpleToastError(ToastText.ALREADY_EXISTS);
        }
        else if (error.includes(ErrorType.BAD_LOGIN)) {
            this.simpleToastError(ToastText.BAD_LOGIN);
        }
        else if (error.includes(ErrorType.USER_ALREADY_ACTIVATED)) {
            this.simpleToastError(ToastText.USER_ALREADY_ACTIVATED);
        }
        else if (error.includes(ErrorType.BAD_CREDENTIALS)) {
            this.simpleToastError(ToastText.BAD_PASSWORD);
        }
        else if (error.includes(ErrorType.USER_DISABLED)) {
            this.simpleToastError(ToastText.USER_DISABLED);
        }
        else if (error.includes(ErrorType.OBJECT_IN_USE)) {
            this.simpleToastError(ToastText.OBJECT_IN_USE);
        }
        else if (error.includes(ErrorMessage.INVALID_DATE)) {
            this.simpleToastError(ToastText.DATE_MUST_BE_FUTURE);
        }
        else if (error.includes(ErrorType.USER_NOT_FOUND)) {
            this.simpleToast(ErrorMessage.USER_NOT_FOUND);
        }
        else if (error.includes(ErrorType.USER_NOT_FOUND)) {
            this.simpleToast(ErrorMessage.USER_NOT_FOUND);
        }
        else if (error.includes(ErrorType.USER_NOTFOUND)) {
            this.simpleToast(ErrorMessage.USER_NOT_FOUND);
        }
        else {
            this.simpleToast(error + '\n' + ToastText.SOMETHING_WENT_WRONG)
        }
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

}