import { Component, OnInit } from '@angular/core';
import { ModalController, PopoverController, NavParams } from '@ionic/angular';
import { UsersModalPage } from '../users-modal/users-modal.page';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Router } from '@angular/router';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { Team } from 'src/models/Team';
import { User } from 'src/models/User';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { ToastText } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-teams-menu',
  templateUrl: './teams-menu.component.html',
  styleUrls: ['./teams-menu.component.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class TeamsMenuComponent {


  private readonly logHeader = "TeamsPage>>";


  private pending: boolean = false;
  public teams: Array<Team> = new Array<Team>();
  private team: Team;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public modalController: ModalController,
    private router: Router,
    private api: BackendApiProvider,
    private popoverController: PopoverController,
    private auth: AuthenticationService,
    private utils: Utils,
    private pageDataService: PageDataService,
    private navParams: NavParams
  ) {

    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.pageDataService.listUserEvent.subscribe(() => {
      this.getTeamById(this.team.getId());
    })
    this.team = this.navParams.get('team');
    this.teams = this.navParams.get('teams');
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async newUser() {
    const modal = await this.modalController.create({
      component: UsersModalPage
    });
    await modal.present();
    this.popoverController.dismiss()
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  pendingUser() {
    this.router.navigate(['/pending']);
    this.popoverController.dismiss()
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isLeader() {
    return this.auth.loggedUser.getId() == this.team.getLiderId();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getTeamById(input: number) {
    this.api.getTeamById(input).then((team: Team) => {
      this.team = team;
      this.api.getUserById(team.getLiderId()).then((leader: User) => {
        this.popoverController.dismiss({ team: team, leader: leader });
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }


}
