import { User } from './User';
import { Utils } from 'src/commons/Utils';
import { ErrorMessage } from 'src/commons/Constants';
import { isArray } from 'util';

//-------------------------------------------------------------------------------------------------------------------------------//

export class Team {

    //-------------------------------------------------------------------------------------------------------------------------------//

    private readonly logHeader = "Team>>";

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Fields */

    private id: number;
    private liderId: number;
    private nome: string;
    private membros: Array<User>;
    private numMembros: number;
    private liderNome

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Getters */

    /**
     * Getter $id
     * @return {number}
     */
    public getId(): number {
        return this.id;
    }

    /**
     * Getter $liderId
     * @return {number}
     */
    public getLiderId(): number {
        return this.liderId;
    }

    /**
     * Getter $nome
     * @return {string}
     */
    public getNome(): string {
        return this.nome;
    }

    /**
     * Getter $liderNome
     * @return {string}
     */
    public getLiderNome(): string {
        return this.liderNome;
    }

    /**
     * Getter $membros
     * @return {Array<User>}
     */
    public getMembros(): Array<User> {
        return this.membros;
    }

    public getMembrosCount(): number {
        if(Utils.isVarInvalid(this.numMembros)){
            let membros: Array<number> = new Array<number>();
            for (let membro of this.membros) {
                membros.push(membro.getId());
            }
            return membros.length;
        }
        else{
            return this.numMembros;
        }
    }


    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    /* #region  Setters */

    /**
     * Setter $id
     * @param {number} value
     */
    public setId(value: number) {
        this.id = value;
    }

    /**
     * Setter $liderId
     * @param {number} value
     */
    public setLiderId(value: number) {
        this.liderId = value;
    }

    /**
     * Setter $nome
     * @param {string} value
     */
    public setNome(value: string) {
        this.nome = value;
    }

    /**
     * Setter $membros
     * @param {Array<User>} value
     */
    public setMembros(value: Array<User>) {
        this.membros = value;
    }

    /* #endregion */

    //-------------------------------------------------------------------------------------------------------------------------------//

    public constructor(
        nome: string = undefined,
        membros: Array<User> = undefined,
        liderId: number = undefined,
        numMembros: number = undefined,
        liderNome: string = undefined,
        id: number = undefined
        ) {
        const logHeader = this.logHeader + "constructor>>";
        Utils.log(logHeader);
        
        this.nome = nome;
        this.id = id;
        this.liderId = liderId;
        this.membros = membros;
        this.numMembros = numMembros;
        this.liderNome = liderNome;
    }

    //-------------------------------------------------------------------------------------------------------------------------------//

    static castFromJSON(input: any): Team {
        const logHeader = "Team>>castFromJSON>>";
        Utils.log(logHeader);

        if (Utils.isVarInvalid(input)) {
            throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + "INPUT>>" + input);
        }
        let json: any;
        if (input.constructor.name === "string" || input.constructor.name === "String") {
            json = JSON.parse(input);
        }
        else {
            json = input
        }
        let id = json.id;
        let nome = json.nome;
        let liderId = json.liderId;
        let membros: Array<User> = new Array<User>();
        let numMembros = json.numMembros;
        let liderNome = json.liderNome
        if (json.membros != null) {
            for (let stringUser of json.membros) {
                membros.push(User.castFromJSON(stringUser));
            }
        }
        return new Team(nome, membros, liderId, numMembros, liderNome, id);
    }


}